<template>
	<div id="container">
		<div class="top">
			<el-input placeholder="请输入内容" v-model="souObj.select_title" class="input-with-select">
				<el-select v-model="souObj.select" slot="prepend" placeholder="请选择">
					<el-option label="用户名" value="user_id"></el-option>
					<el-option label="店主名" value="name"></el-option>
					<el-option label="区号" value="area_code"></el-option>
					<el-option label="全部" value="all"></el-option>
				</el-select>
				<el-button label="dsadasd" slot="append" icon="el-icon-search" @click="sousuo"></el-button>
			</el-input>
			<el-button type="primary" @click="addObj.flag  = true">创建用户</el-button>
		</div>
		<el-table 
			stripe
			:data="tableData" 
			style="width: 100%"
			max-height='730px'
			v-if='tableData'
			v-loading="loading"
			>
			<el-table-column prop="name" label="店名"> </el-table-column>
			<el-table-column prop="area_code" label="区号"> </el-table-column>
			<el-table-column prop="dizhi" label="地址"> </el-table-column>
			<el-table-column prop="if_" label="状态"> </el-table-column>
			<el-table-column prop="bank" label="银行"> </el-table-column>
			<el-table-column prop="card_number" label="银行卡号"> </el-table-column>
			<el-table-column fixed="right" label="操作" width="180">
			  <template slot-scope="scope">
				<el-button size="mini" @click="handleEdit(scope.$index, scope.row);">编辑</el-button>
				<el-button size="mini" @click.native.prevent="handleDelete(scope.$index, scope.row)">移除</el-button>
			  </template>
			</el-table-column>
		</el-table>
		<!-- 添加 -->
		<el-dialog title="创建门店" :visible.sync="addObj.flag" width="40%" center>
			<el-form label-position="left" label-width="100px">
				<el-form-item label="门店主:">
					 <el-select v-model="addObj.mdname" filterable placeholder="选择您的门店主">
					    <el-option
					      v-for="item in userList"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select>
				</el-form-item>
				<el-form-item label="区号:">
					<el-input v-model="addObj.area_code" type="number" size="small"></el-input>
				</el-form-item>
				<el-form-item label="门店名:">
					<el-input v-model="addObj.ename" type="text" size="small"></el-input>
				</el-form-item>
				<el-form-item label="门店营业状态:">
					<el-radio-group v-model="addObj.type">
						<el-radio label="禁止"></el-radio>
						<el-radio label="未营业"></el-radio>
						<el-radio label="营业"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="收款银行:">
					<el-input v-model="addObj.bank" type="text" size="small"></el-input>
				</el-form-item>
				<el-form-item label="银行卡号:">
					<el-input v-model="addObj.card_number" type="number" size="small"></el-input>
				</el-form-item>
				<el-form-item label="地址:">
					 <el-cascader
						placeholder="找不到？试试搜索:"
						:options="space"
						filterable
						v-model="addObj.dizhi"></el-cascader>
				</el-form-item>
				<el-form-item label="详细地址:">
					<el-input
					  type="textarea"
					  :rows="2"
					  placeholder="请输入内容"
					  v-model="addObj.address">
					</el-input>
				</el-form-item>	
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addClear()">取 消</el-button>
				<el-button type="primary" @click="addTrue()">确 定</el-button>
			</div>
		</el-dialog>
		<!-- 编辑 -->
		<el-dialog title="编辑信息" :visible.sync="changObj.flag" width="50%" center>
		  <el-form label-position="left" label-width="80px">
			<el-form-item label="门店名:">
			  <el-input v-model="changObj.name"  size="large"></el-input>
			</el-form-item>
			<el-form-item label="营业状态:">
				<el-radio-group v-model="changObj.if_">
					<el-radio label="禁止"></el-radio>
					<el-radio label="未营业"></el-radio>
					<el-radio label="营业"></el-radio>
				</el-radio-group>
			</el-form-item>
			<el-form-item label="银行:">
			  <el-input v-model="changObj.bank"></el-input>
			</el-form-item>
			<el-form-item label="银行卡号:">
			  <el-input v-model="changObj.card_number"></el-input>
			</el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
			<el-button @click="changObj.flag = false">取 消</el-button>
			<el-button type="primary" @click="changTrue">确 定</el-button>
		  </div>
		</el-dialog>
		<el-pagination
		  background
		  layout="sizes, prev, pager, next, jumper"
		  :total="fenye.total"
		  :page-sizes="[10,20,30,50]"
		  @current-change="yebian"
		  @size-change='tiaobian'
		  :page-size="fenye.page_size"
		  :current-page="fenye.current_page">
		</el-pagination>
	</div>
</template>
<script>
 export default {
	data() {
		return {
			tableData: [{name:''}],
			space:'',
			souObj:{
				select_title:'',
				select:'all'
			},
			loading:true,
			userList:[],//用户列表
			fenye:{//分页
				total:1,
				page_size:10,
				current_page:1
			},
			addObj:{//添加门店
				user_id:'',
				ename:'',
				area_code:'',
				dizhi:'',
				type:'营业',
				bank:'',
				card_number:'',
				address:'',
				flag:false
			},
			changObj:{//编辑信息
				flag:false,
				name:'',
				if_:'',
				bank:'',
				card_number:'',
				id:''
			}
		}
	},
	methods:{
		//搜索
		sousuo(){
			if(this.souObj.select){//判断搜索的条件存不存在，要不浪费时间
				this.loading = true
				if(this.souObj.select != 'all'){
					let that = this;
					let obj = {
						'name':this.$store.state.userInfo.name,
						'token':this.$store.state.userInfo.token,
						'page':1,
						'pageSize':this.fenye.page_size,
						'select':this.souObj.select,
						'select_title':this.souObj.select_title
					}
					this.$axios.post("/shopinfo_select",obj).then(function(res){
						that.tableData = res.data.data
						that.tableData.forEach(function(val,ind){
							val.dizhi = val.province + val.city + val.address
							if(val.if_ == 1){
								val.if_ = '营业' 
							}else if(val.if_ == -1){
								val.if_ = '禁止'
							}else{
								val.if_ = '未营业'
							}
						})
						that.souObj.select_title = ''
						that.fenye.page = 1
						that.fenye.total = res.data.pageCount * res.data.pageSize
						that.$message({
							type: 'success',
							message: res.data.message,
						}); 
						that.loading = false
					})
				}else{
					this.current_page = 1
					this.page_size = 10
					this.requestList(this.current_page,this.page_size)
				}
			}else{
				this.$message({
					type: 'error',
					message: '请先选择搜索项'
				}); 
			}
		},
		//编辑
		handleEdit(index, row) {
			this.changObj.flag = true
			this.changObj.name = this.tableData[index].name
			this.changObj.if_ = this.tableData[index].if_
			this.changObj.bank = this.tableData[index].bank
			this.changObj.card_number = this.tableData[index].card_number
			this.changObj.id = this.tableData[index].id
		},
		//编辑确定按钮
		changTrue(){
			this.loading = true
			let that = this
			let obj = {
				ename:this.changObj.name,
				province:this.changObj.province,
				bank:this.changObj.bank,
				card_number:this.changObj.card_number,
				token:this.$store.state.userInfo.token,
				name:this.$store.state.userInfo.name,
				id:this.changObj.id
			}
			if(this.changObj.if_ == '营业'){
				obj.if_ = 1
			}else if(this.changObj.if_ == '禁止'){
				obj.if_ = -1
			}else{
				obj.if_ = 0	
			}
			console.log(obj.if_)
			this.$axios.post("/shopinfoEdit",obj).then(function(res){
				if(res.data.code == '0'){
					that.requestList(that.fenye.current_page,that.fenye.page_size)
					that.changObj.flag = false
					that.$message({
						type: 'success',
						message: res.data.message,
					}); 
				}else{
					that.changObj.flag = false
					that.$message({
						type: 'info',
						message: res.data.message
					});
				}
			})
		},
		//分页的页数发生变化
		yebian(currentPage){
			this.loading = true
			this.fenye.current_page = currentPage
			this.requestList(currentPage,this.fenye.page_size)
		},
		//分页每页条数发生变化
		tiaobian(ind){
			this.loading = true
			this.fenye.page_size = ind
			this.requestList(this.fenye.current_page,ind)
		},	
		//添加确定
		addTrue(){
			this.loading = true
			let that = this 
			let obj = {}
			//将收集的内容进行一些处理
			//账号的名字
			obj.name = this.$store.state.userInfo.name
			//账号的token（目的是为了验证，到底是那个人提交的信息）
			obj.token = this.$store.state.userInfo.token
			//地址的拆分（省，市）
			obj.province = this.addObj.dizhi[0]
			obj.city = this.addObj.dizhi[1]
			//营业状态
			if(this.addObj.type == '营业'){
				obj.if_ = 1
			}else if(this.addObj.type = '禁止'){
				obj.if_ = -1
			}else{
				obj.if_ = 0
			}
			obj.area_code = this.addObj.area_code
			obj.ename = this.addObj.ename
			obj.address = this.addObj.address
			obj.bank = this.addObj.bank
			obj.card_number = this.addObj.card_number
			
			this.$axios.post("/shopinfoAdd",obj).then(function(res){
				//创建用户名成功
				if(res.data.code == '0'){
					that.requestList(that.fenye.current_page,that.fenye.page_size)
					that.$message({
						type: 'success',
						message: res.data.message,
					}); 
				}else{
				//创建用户名失败
					that.$message({
						type: 'info',
						message: res.data.message
					});
				}
				that.addClear()
			})
		},
		//添加清空
		addClear(){
			this.addObj.name = ''
			this.addObj.ename = ''
			this.addObj.area_code = ''
			this.addObj.dizhi = ''
			this.addObj.type = '营业'
			this.addObj.bank = ''
			this.addObj.card_number = ''
			this.addObj.address = ''
			this.addObj.flag = false
		},
		//移除
		handleDelete(index, row) {
			this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.tableData.splice(index,1)
				this.$message({
					type: 'success',
					message: '删除成功!'
				});
			}).catch(() => {
				this.$message({
					type: 'info',
					message: '已取消删除'
				});          
			});
		},
		//获取门店的列表
		requestList(ind,num){
			let that = this;
			let obj = {
				'name':this.$store.state.userInfo.name,
				'token':this.$store.state.userInfo.token,
				'page':ind,
				'pageSize':num
			}
			this.$axios.post("/shopinfo",obj).then(function(res){
				that.tableData = res.data.data
				that.tableData.forEach(function(val,ind){
					val.dizhi = val.province + val.city + val.address
					if(val.if_ == 1){
						val.if_ = '营业' 
					}else if(val.if_ == -1){
						val.if_ = '禁止'
					}else{
						val.if_ = '未营业'
					}
				})
				that.fenye.total = res.data.pageCount * res.data.pageSize
				that.loading = false
			})
		}
	},
	created() {
		//获取vuex中地址的信息
		this.space = this.$store.state.space
		//获取用户列表
		let that = this;
		let obj = {
			'name':this.$store.state.userInfo.name,
			'token':this.$store.state.userInfo.token
		}
		this.$axios.post("/user",obj).then(function(res){
			res.data.data.forEach(function(val,ind){
				let arr = String(val.q_id).split("")
				for(let i = 0;i < arr.length;i++){
					if(arr[i] == '4'){
						that.userList.push({
							'label':val.name,
							'value':val.id
						})
						break
					}
				}
			})
		})
		//获取门店的列表
		this.requestList(this.current_page,this.page_size)
	}
}
</script>
<style lang="less">
#container{
  padding-right: 10px;
}
.top{
  width:100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.el-table-column label{
	text-align: center;
}
.el-input-group{
	width: 50%;
}
.el-select .el-input {
	width: 130px;
}
.input-with-select .el-input-group__prepend {
	background-color: #fff;
}
</style>

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          